<template>
  <div class="sliderWin">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
      <p class="cy_title">示例效果</p>
        <div class="iframeBox">
          <iframe src="#/sliderWinExample"></iframe>
        </div>
    </div>   
    <div class="cy_center">
      <h2 class="cy_h2">sliderWin 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import sliderWin from "cy_static/sliderWin"; 
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
  &lt;style type="text/css"&gt;
    #myWin{width:375px;position:relative;height:300px;overflow:hidden;background-color:#fff;}
    #myWin .content{height: 100%;background-color:#e0eeee}
  &lt;/style&gt;

  &lt;div id="myWin"&gt;
      &lt;div class="content"&gt;
        中国人 
        五千年的风和雨啊 藏了多少梦
      &lt;/div&gt;
      &lt;div class="content"&gt;
        黄色的脸 黑色的眼  不变是笑容
        八千里山川河岳  像是一首歌
      &lt;/div&gt;
  &lt;/div&gt;

  &lt;script type="text/javascript"&gt;
      import sliderWin from "cy_static/sliderWin"; 
      sliderWin({
        elem : "#myWin", // 窗口元素--要设置高度
        content : ".content"
      });
  &lt;/script&gt;
</xmp >
  
      </div>

      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
        <h2 class="cy_h2">构造方法</h2>				
            <p><a>sliderWin</a>(params : Object) : <a>Object</a></p>
						<p>窗口滑动切换、暂停、动态设置当前窗口；</p>
            <table class="table-props">
              <tr>
                <th>参数名</th>
                <th>类型</th>
                <th>描述</th>
                <th>默认值</th>
                <th class="text-c">必需</th>
              </tr>
              <tr>
                <td>elem</td>
                <td class="atn">String||Object</td>
                <td>窗口元素</td>
                <td>""</td>
                <td class="text-c">Y</td>
              </tr>
              <tr>
                <td>content</td>
                <td class="atn">String||Object</td>
                <td>窗口子元素</td>
                <td>""</td>
                <td class="text-c">Y</td>
              </tr>
              <tr>
                <td>time</td>
                <td class="atn">Number</td>
                <td>滑动时间(毫秒)</td>
                <td>650</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>springDrag</td>
                <td class="atn">Number</td>
                <td>超出窗体阻尔糸数</td>
                <td>0.8</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>resetWidth</td>
                <td class="atn">Number</td>
                <td>重置复位宽度(px)</td>
                <td>40</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>currIndex</td>
                <td class="atn">Number</td>
                <td>默认选中的窗口</td>
                <td>0</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>isOverMove</td>
                <td class="atn">Boolean</td>
                <td>超过窗口是否能滑动</td>
                <td>true</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onStart</td>
                <td class="atn">Function</td>
                <td>拖动开始</td>
                <td>function (left, event, params) {}</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onScroll</td>
                <td class="atn">Function</td>
                <td>滚动事件</td>
                <td>function (index, dom, left) {}</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onMove</td>
                <td class="atn">Function</td>
                <td>拖动中</td>
                <td>function (left, event, params) {}</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onStop</td>
                <td class="atn">Function</td>
                <td>拖动结束</td>
                <td>function (left, event, params) {}</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onChange</td>
                <td class="atn">Function</td>
                <td>窗口改变事件</td>
                <td>function (index) {}</td>
                <td class="text-c">N</td>
              </tr>
            </table>
      </div>

      <div class="apiModule">
        <h2 class="cy_h2">实例方法</h2>
        <ul class="ul-props">
					<li>
						<p><a>pause</a>() : <a>Void</a></p>
						<p>禁止滑动；</p>
					</li>
					<li>
						<p><a>play</a>() : <a>Void</a></p>
						<p>启动滑动；</p>
					</li>
					<li>
						<p><a>getCurrIndex</a>() : <a>Number</a></p>
						<p>获得当前激活的窗口</p>
					</li>		
					<li>
						<p><a>setActivated</a>(isActivated) : <a>Void</a></p>
						<p>是否激活；</p>
					</li>
					<li>
						<p><a>destroy</a>() : <a>Void</a></p>
						<p>销毁实例里所有事件；</p>
					</li>
					<li>
						<p><a>setCurrIndex</a>(index : Number) : <a>Void</a></p>
						<p>设置要选中窗口下标（从0开始）；并在可视窗口中显示</p>
					</li>
				</ul>        
      </div>




    </div> 
  </div>
</template>

<script>
export default {
  name: 'sliderWin',
  data () {
    return {
      msg: 'sliderWin',
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  },  
  mounted(){
    var m = this;
    m.$refs.codeStyle1.codemirror.setValue(m.$refs.codeStyleVal1.innerHTML.replace(/^\s|\s+$/g,''));
    m.$refs.codeStyle2.codemirror.setValue(m.$refs.codeStyleVal2.innerHTML.replace(/^\s|\s+$/g,''));
  }
}
</script>    
<style scoped>
  .cy_example .iframeBox{position:absolute;top:43px;width:100%;bottom:0}
</style>


